.coord-anchor{width:100vw;display:grid;grid-template-columns:repeat(8, 12.5%);grid-template-rows:repeat(8, 12.5%);height:100vh;position:fixed}.coord-container{height:46.8vh;overflow:hidden;grid-area:3/1/3/9;margin-top:0.8vw}.coord-movingBackground{transform:rotate(180deg)}.coord-editor{z-index:5;box-shadow:0 0 58px #000000d1;overflow:scroll;grid-area:1/4/9/6;width:150%;display:inline-flex;flex-direction:column;margin-left:-6vw;background:#21212cc2;align-items:center}.coord-heading{position:relative;text-align:center;margin-top:2vh;line-height:6vh;font-family:Major Mono Display;font-size:3vh;color:#ff0078}.coord-infoWrapper{position:relative;display:-webkit-box;display:-webkit-flex;display:-ms-flexbox;display:flex;-webkit-box-pack:center;-webkit-justify-content:center;-ms-flex-pack:center;justify-content:center;background:#00000075;border-radius:8vh 8vh 5vh 5vh;height:5vh;line-height:2vh;box-shadow:inset 0px 0px 1.4vh 0px #048ffc7d}.coord-info{font-size:3vh;color:white;font-weight:500;margin:2vw 0}.coord-new{margin-top:2vh;height:4vh;width:15vw;text-align:center;font-size:2.5vh;font-family:Major Mono Display;border-radius:0.4vh 0.4vh 1vh 1vh;box-shadow:inset 0 0 1vh black;background:#ff006a3b;color:white;font-weight:600;margin-bottom:3vh}.coord-travel{position:relative;width:6vh;margin-left:3vh;height:5.6vh;border-radius:100%;margin-top:-0.3vh;background:#007effc4;border:0.06vh solid white;box-shadow:inset 0 0 1.5vh black;color:#ffffff;cursor:pointer}
